$color-primary: #ffeb3b;
$color-primary-dark: darken($color-primary, 20);
$color-primary-light: #ffff72;
$color-secondary: #2979ff;
$color-secondary-dark: darken($color-secondary, 20);
$color-secondary-light: #75a7ff;
$color-text: #333333;

$breakpoints: (
  xs: 0px,
  sm: 600px,
  md: 960px,
  lg: 1280px,
  xl: 1920px,
);
@import 'scss/mediaqueries';

$spacing: 8px;

@function spacing($factor) {
  @return $factor * $spacing;
}

img {
  width: 100%;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  color: $color-text;
}
.primary {
  color: $color-primary;
}

.secondary {
  color: $color-secondary;
}

.section {
  min-height: 100vh;
  display: flex;
  align-items: center;

  &.hero {
    position: relative;
    background-size: cover;
    background-position: center;
    color: #fff;
    .filter {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-color: $color-secondary;
      opacity: 0.4;
      z-index: 0;
    }
    .title {
      color: $color-primary;
    }
    .subtitle {
      color: #fff;
    }
  }
  &--primary {
    background-color: darken($color-primary, 10);
    .title {
      color: $color-secondary;
    }
    .subtitle {
      color: #fff;
    }
  }
  &.interlude {
    min-height: auto;
  }
}

.spacer {
  height: spacing(4);
}

.content {
  position: relative;
  width: 100%;
  margin: 0 auto;
  padding: spacing(1);
}

.title {
  font-family: 'Open Sans', sans-serif;
  color: $color-secondary-dark;
  font-size: 2rem;
  line-height: 1.6;
}
.subtitle {
  color: $color-primary-dark;
  font-size: 1.6rem;
  line-height: 1.1;
  margin-bottom: spacing(2);
}

.button {
  padding: spacing(2) spacing(3);
  background-color: $color-primary;
  text-decoration: none;
  color: $color-text;
  transition: all 0.1s ease-in-out;

  &:hover {
    background-color: $color-primary-dark;
  }

  &--main {
    z-index: 100;
    position: fixed;
    top: spacing(2);
    right: spacing(2);
  }

  &--secondary {
    background-color: $color-secondary;
    color: #fff;
    &:hover {
      background-color: $color-secondary-dark;
    }
  }
}

@include media-up(sm) {
  body {
    font-size: 18px;
  }
  .content {
    width: map-get($breakpoints, sm);
    padding: spacing(2);
  }
  .title {
    font-size: 4rem;
  }
  .subtitle {
    font-size: 3rem;
  }
  .text {
    &--wrap {
      column-count: 2;
      column-gap: spacing(3);
    }
  }

  .grid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: 2fr;

    img:first-child {
      grid-row: 1 / 3;
      grid-column: 1 / 2;
    }

    &.reverse {
      grid-template-columns: 1fr 2fr;
      img:first-child {
        grid-row: unset;
        grid-column: unset;
      }
      img:last-child {
        grid-row: 1 / 3;
        grid-column: 2 / 3;
      }
    }
  }
}
@include media-up(md) {
  .content {
    width: map-get($breakpoints, md);
    padding: spacing(3);
  }
}
